
import{mapMutations} from 'vuex';
export default {
	methods:{
		...mapMutations(['login'])
	},
	onLaunch: function() {
		// console.log('App Launch')
		let userId=uni.getStorageSync('userId') || '';
		if(userId){
			uni.setStorage({
				key: "userId",
				data: userId
			})
		}
	},
	onShow: function() {
		// console.log('App Show')
	},
	onHide: function() {
		// console.log('App Hide')
	}
}
